import reviewService from '@/services/review.service'

export default {
    state: {
        reviews: []
    },

    getters: {
        reviewsToShow(state) {
            return state.reviews
        }
    },

    mutations: {
        updateReviews(state, {reviews}) {
            state.reviews = reviews;
        },

        addReview(state, {review}) {
            state.reviews.push(review)
        }
    },

    actions: {
        saveReview(context, { review }) {
            return reviewService.addReview(review)
                .then(_ => {
                    review.user = context.getters.user
                    review.toy = context.getters.currentToy.toy
                    context.commit({type: 'addReview', review})
                })
    
        },

        loadReviews({ commit }) {
            return reviewService.query()
                .then(reviews => commit({type: 'updateReviews', reviews}))
        },

        setReviews({ commit }, {reviews}) {
            commit({type: 'updateReviews', reviews})
        }
    }
}