<template>
    <section v-if="toy">
        <div>
            Toy name: {{toy.name}}
        </div>
        <div>
            Toy price: {{toy.price}}
        </div>
        <div>
            Toy Type: {{toy.type}}
        </div>
        <div>
            In Stock? {{toy.inStock? 'Yes' : 'No'}}
        </div>
        <div>
            Added to store at: {{toy.createdAt}}
        </div>
        <template  v-if="reviews.length > 0">
            <p>Reviews for this toy:</p>
            <review-list :reviews="reviews"></review-list>
        </template>
        <template v-if="user._id">
            <form @submit.prevent="saveReview">
                <textarea type="text" v-model="review.content" placeholder="Enter Review"/>
                <button>Publish Review</button>
            </form>
        </template>
        <router-link to="/toy">Back to Toys list</router-link>
    </section>
</template>

<script>

import reviewList from '@/components/review-list'

export default {
  name: "toy-details",
  data() {
    return {
        toy: null,
        // reviews: [],
    };
  },

  components: {
      reviewList,
  },

  computed: {
      user() {
          return this.$store.getters.user
      },

      review() {
          return {
              content: '',
              toyId: this.toy._id,
              userId: this.user._id
          }
        },

        reviews() {
            return this.$store.getters.reviewsToShow
        }
  },

  methods: {
      saveReview() {
          this.$store.dispatch({type: 'saveReview', review: this.review})
      }
  },

  created() {
    const toyId = this.$route.params.toyId;
    if (toyId) {
      this.$store.dispatch({ type: "getById", toyId }).then(toyData => {
        this.toy = toyData.toy;
        this.$store.dispatch({type: 'setReviews', reviews: toyData.reviews})
        // this.reviews = toyData.reviews;
      });
    }
  }
};
</script>
