<template>
    <section>
        <h1>Welcome to Toy-Store</h1>
        <template v-if="isLoggedinUser">
            <div>How are you today 
                <router-link :to="'/user/' + user._id">{{user.username}}</router-link>
            </div>
            <router-link to="/toy/edit" v-if="user.isAdmin">Add New Toy</router-link>
            <router-link to="/toy/stats">Toy Statistics</router-link>
            <router-link to="/review">See toy reviews</router-link>
            <toy-filter></toy-filter>
            <toy-list :toys="toys" :user="user" @removeToy="removeToy"></toy-list>
        </template>
        <router-link v-else to="/">Log In</router-link>
    </section>
</template>

<script>
import toyList from '@/components/toy-list.vue'
import toyFilter from '@/components/toy-filter.vue'

export default {
    name: 'toys-app',
    data() {
        return {
            
        }
    },

    components: {
        toyList,
        toyFilter,
    },

    computed: {
        toys() {
            return this.$store.getters.toys
        },

        user() {
            return this.$store.getters.user
        },

        isLoggedinUser() {
            return !!this.user._id
        }
    },

    methods: {
        removeToy(toyId) {
            this.$store.dispatch({type: 'removeToy', toyId})
        }
    },

    created() {
        this.$store.dispatch({type: 'loadToys'})
    }
}
</script>
