import toyService from '@/services/toy.service'


export default {
    state: {
        toys: [],
        currentToy: null,
        types: [],
        typesCount: [],
        filter: {
          byName: '',
          byStatus: 'all',
          byType: 'all',
          sort: 'name_1',
        },
        
      },
    
      getters: {
        toys(state) {
          return state.toys
        },
    
        types(state) { 
          return state.types
        },
    
        typesCount(state) {
          return state.typesCount
        },

        currentToy(state) {
            return state.currentToy
        }
      },
    
      mutations: {
        setToys(state, {toys}) {
          state.toys = toys;
        },
    
        setTypes(state) {
          state.toys.forEach(toy => {
            if(!state.types.find(type => type === toy.type)) {
              state.types.push(toy.type)
            }
          })
        },
    
        setTypesCount(state, {types}) {
          state.typesCount = types
        },
    
        updateToys(state, {toyId}) {
          const toyIdx = state.toys.findIndex(toy => toy._id === toyId)
          state.toys.splice(toyIdx, 1)
        },
    
        // setToy(state, {toy}) {
        //   const toyIdx = state.toys.find(currToy => currToy.id === toy.id)
        //   if (toyIdx === -1) {
        //     state.toys.push(toy)
        //   } else {
        //     state.toys.splice(toyIdx, 1, toy)
        //   }
        // },
    
        updateFilter(state, {filter}) {
          state.filter = filter
        },

        setCurrentToy(state, {toy}) {
            state.currentToy = toy;
        }
      },
    
      actions: {
        loadToys({state, commit, dispatch}) {
          // dispatch({type: 'getTypesCount'})
          return toyService.query(state.filter)
            .then(toys => {
              commit({type: 'setToys', toys})
              commit({type: 'setTypes'})
            })
        },
    
        removeToy({commit}, {toyId}) {
          return toyService.remove(toyId)
            .then(_ => {
              commit({type: 'updateToys', toyId})
            })
        },
    
        getById(context, {toyId}) {
          return toyService.getById(toyId)
            .then(toy => {
                context.commit({type: 'setCurrentToy', toy})
                return toy
            })
        },
    
        saveToy({commit}, {toy}) {
          return toyService.saveToy(toy)
            // .then(toy => {
            //   commit({type: 'setToy', toy})
            // })
        },
    
        setFilter({dispatch, commit}, {filter}) {
          commit({type: 'updateFilter', filter})
          dispatch({type: 'loadToys'})
        },
    
        getTypesCount({commit}) {
          return toyService.typesQuery()
          .then(types => {
            commit({type: 'setTypesCount', types})
          })
        },
      }
}