<template>
    <section v-if="user">
        <p>Hi {{user.username}}</p>
        <template  v-if="reviews.length > 0">
            <p>Your Reviews:</p>
            <review-list :reviews="reviews"></review-list>
        </template>
        <router-link to="/toy">Back to Toys list</router-link>
    </section>
</template>

<script>

import reviewList from '@/components/review-list'

export default {
    name: 'user-details',
    data() {
        return {
            user: null,
            // reviews: []
        }
    },

    components: {
        reviewList
    },

    computed: {
        reviews() {
            return this.$store.getters.reviewsToShow
        }
    },

    created() {
        const userId = this.$route.params.userId
        if (userId) {
            this.$store.dispatch({type: 'getUserById', userId})
                .then(userData => {
                    this.user = userData.user
                    this.$store.dispatch({type: 'setReviews', reviews: userData.reviews})
                    // this.reviews = userData.reviews
                })
        }
    }
}
</script>

