<template>
    <section>
        <form @submit.prevent="login">
            <input type="text" v-model="username" placeholder="Enter Username">
            <button>Log In</button>
        </form>
    </section>
</template>

<script>
export default {
    name: 'login-page',
    data() {
        return {
            username: ''
        }
    },

    methods: {
        login() {
            this.$store.dispatch({type: 'login', username: this.username})
                .then(user => {
                    if (user) {
                        this.$router.push('/toy')
                    }
                })
        },
    }
}
</script>

