const axios = require('axios')

const BASE_URL = (process.env.NODE_ENV !== 'development')
                    ? ''
                    : '//localhost:3000';

export default {
    query,
    addReview,
}

function query() {
    return axios.get(`${BASE_URL}/review`)
        .then(res => res.data)
}

function addReview(review) {
    return axios.post(`${BASE_URL}/review`, review)
        .then(res => res.data)
}